.js30-content {
  input {
    width: 100%;
    padding: 20px;
  }
  
  .search-form {
    max-width: 400px;
    margin: 50px auto;
  }
  
  input.search {
    margin: 0;
    text-align: center;
    outline: 0;
    border: 10px solid #F7F7F7;
    width: 120%;
    left: -10%;
    position: relative;
    top: 10px;
    z-index: 2;
    border-radius: 5px;
    font-size: 40px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.12), inset 0 0 2px rgba(0, 0, 0, 0.19);

    @media screen and (max-width: 767px) {
          width: 110%;
          left: -5%;
    }
  }
  
  .suggestions {
    margin: 0;
    padding: 0;
    position: relative;
  }
  
  .suggestions li {
    background: rgba(199, 199, 199, 0.21);
    list-style: none;
    border-bottom: 1px solid #777;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.14);
    margin: 0;
    padding: 20px;
    transition: background 0.2s;
    display: flex;
    justify-content: space-between;
  }
  
  .suggestions li span {
    text-transform: capitalize;
  }
  
  .suggestions li:nth-child(even) {
    transform: perspective(100px) rotateX(3deg) translateY(2px) scale(1.001);
    background: linear-gradient(to bottom,  rgba(199, 199, 199, 0.21) 0%, rgba(199, 199, 199, 0.35) 100%);
  }
  
  .suggestions li:nth-child(odd) {
    transform: perspective(100px) rotateX(-3deg) translateY(3px);
    background: linear-gradient(to top,  rgba(199, 199, 199, 0.35) 0%, rgba(199, 199, 199, 0.21) 100%);
  }
  
  span.population {
    font-size: 15px;
  }
  
  .hl {
    background: #070707;
    padding: 3px 2px;
  }
  
  .hl.tt {
    text-transform: initial;
  }
  
  .hl:empty {
    display: none;
  }
}

